@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Alef:wght@400;700&display=swap);
:root {
    --light: #fefefe;
    --dark: #282c34;
    --prime: #61dafb;
    --prime2: #61dafb;
  }

/* ==== FORM ====  */
form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    min-width: 250px;
    max-width: 400px;
  }

  form.middle {
    width: 50vw;
  }

  label, input, button {
    padding: 0;
    margin: 0;
    border-width: 1px;
  }
  
  form input, form label, form button {
    width: 100%;
  }
  form input {
    line-height: 1.8rem;
    margin-top: 5px;
  }
  form input:focus {
      -webkit-filter: drop-shadow(-1px 3px 7px #dedede) drop-shadow(-4px 0px 7px #dedede);
              filter: drop-shadow(-1px 3px 7px #dedede) drop-shadow(-4px 0px 7px #dedede);
  }
  form label, form button {
    margin-top: calc(1rem + 6px);
    text-transform: capitalize;
  }
  form label {
    font-weight: 500;
    color: #222222;
  }
  
  form button {
    border-width: 1px;
    border-color: dodgerblue;
    line-height: 2rem;
    color: #fff;
    background: dodgerblue;
    font-size: 1.12rem;
    letter-spacing: -0.24px;
    -webkit-filter: drop-shadow(-4px 0px 7px dodgerblue);
            filter: drop-shadow(-4px 0px 7px dodgerblue);
    transition: all 250ms ease-in-out 50ms;
  }
  form button:hover {
    border-color: gray;
    -webkit-filter: hue-rotate(45deg) drop-shadow(-4px 0px 7px grey);
            filter: hue-rotate(45deg) drop-shadow(-4px 0px 7px grey);
  }
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
    margin-bottom: .5rem;
    font-family: inherit;
    font-weight: 500;
    line-height: 1.2;
    color: inherit;
}

h1, h2, h3, h4, h5, h6 {
    margin-top: 0;
    margin-bottom: .5rem;
}

h1, h2, h3, .h1, .h2, .h3, input {
    font-family: 'Alef', 'Arial', sans-serif;
}

span, div, p , .info, .desc , a, 
h4, h5, h6 , .h4, .h5, .h6 , button {
    font-family: 'Montserrat', 'Roboto',  'Helvetica Neue', -apple-system, sans-serif;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
      monospace;
}

  a {
    font-size: 1rem;
}


.pointer {
  cursor: pointer;
}

.capitalized {
    text-transform: capitalize;
}

.bold {
  font-weight: bold;
}
body {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
ul, li {
  list-style: none;
}

.container {
  padding-top: 20px;
  display: grid;
  grid-gap: 30px;
  grid-template-columns: repeat(auto-fill, 350px);
  justify-content: center;
}

.container.middle {
  align-items: center;
  grid-template-columns: unset;
}

.container.wide {
  grid-template-columns: repeat(auto-fill, 450px);
}
.photo-grid {
  margin-top: 30px;
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(auto-fill, 300px);
  justify-content: center;
}

/* CARDS */
.card {
  justify-content: center;
  position: relative;
  display: flex;
  flex-direction: column;
}

.card h3.card-title {
  margin: 0;
  margin-top: 5px;
  text-align: center;
}

.card span.card-subtitle {
  font-size: 1.2rem;
  font-weight: 500;
  text-align: center;
}
.card span.card-desc {
  color: #bebebe;
  text-align: center;
  font-size: 0.9rem;
  -webkit-margin-end: 5px;
          margin-inline-end: 5px;
}

.date {
  position: absolute;
  z-index: 5;
  background: #444444;
  padding: 4px 8px;
  color: #fefefe;
}

@-webkit-keyframes fadeBlur {
  from {
    -webkit-filter: blur(18px) opacity(0.5);
            filter: blur(18px) opacity(0.5);
  }
  to {
    -webkit-filter: blur(0) opacity(1);
            filter: blur(0) opacity(1);
  }
}

@keyframes fadeBlur {
  from {
    -webkit-filter: blur(18px) opacity(0.5);
            filter: blur(18px) opacity(0.5);
  }
  to {
    -webkit-filter: blur(0) opacity(1);
            filter: blur(0) opacity(1);
  }
}
@-webkit-keyframes brightBlur {
  from {
    -webkit-filter: blur(18px)  brightness(7.5);
            filter: blur(18px)  brightness(7.5);
  }
  to {
    -webkit-filter: blur(0)  brightness(1);
            filter: blur(0)  brightness(1);
  }
}
@keyframes brightBlur {
  from {
    -webkit-filter: blur(18px)  brightness(7.5);
            filter: blur(18px)  brightness(7.5);
  }
  to {
    -webkit-filter: blur(0)  brightness(1);
            filter: blur(0)  brightness(1);
  }
}
figure.card-photo {
  transition: brightBlur 400ms linear 50ms;
}

figure.card-photo img {
  width: 300px;
  height: 300px;
}

figure.card-photo img:hover {
  -webkit-filter: saturate(0.5);
          filter: saturate(0.5);
}


.App {
  text-align: center;
}

.App-header {
  background-color: var(--dark);
  min-height: 25vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--light);
}

.App-link {
  color: var(--prime2);
}

.App-container {
  margin: 0 30px;
  max-width: 1700px;
  padding: 5px;
}

nav ul{
  display: flex;
  justify-content: center;
  align-items: center;
}
nav a{
  color: var(--light);
  text-decoration: none;
  -webkit-margin-end: 2rem;
          margin-inline-end: 2rem;
  font-size: 1.1rem;
  letter-spacing: -0.42px;
}
 /* ROcket Loader  */
@-webkit-keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@media (prefers-reduced-motion: no-preference) {
  .rocket {
    -webkit-animation: spin infinite 0.45s cubic-bezier(0.55, 0.055, 0.675, 0.19);
            animation: spin infinite 0.45s cubic-bezier(0.55, 0.055, 0.675, 0.19);
    justify-content: center;
    align-items: center;
    margin: 50px auto;
    display: flex;
  }
}


 
/* Ripple Loader: Loading.io  */
.lds-ripple {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-ripple div {
    position: absolute;
    border: 4px solid #fff ;
    opacity: 1;
    border-radius: 50%;
    -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
            animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
  }
  .lds-ripple div:nth-child(2) {
    -webkit-animation-delay: -0.5s;
            animation-delay: -0.5s;
  }
  @-webkit-keyframes lds-ripple {
    0% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0px;
      left: 0px;
      width: 72px;
      height: 72px;
      opacity: 0;
    }
  }
  @keyframes lds-ripple {
    0% {
      top: 36px;
      left: 36px;
      width: 0;
      height: 0;
      opacity: 1;
    }
    100% {
      top: 0px;
      left: 0px;
      width: 72px;
      height: 72px;
      opacity: 0;
    }
  }
  
.alert {
    --danger-color:#721c24;
    --danger-background: #f5c6cb;
    --danger-border: #f8d7da;

    --warning-color: #856404;
    --warning-background: #fff3cd;
    --warning-border: #ffeeba;

    --info-color: #fefefe ;
    --info-background: #007bff;
    --info-border: (0,123,255);
}

.alert {
    position: relative;
    padding: .75rem 1.25rem;
    margin-bottom: 1rem;
    border: 1px solid transparent;
    border-radius: .25rem;
    width: 90%;
    transition: border 3s ease-in-out , width 300ms ease-in-out ;
}

.alert-link {
    font-weight: 700;
}

/* danger */
.alert-danger {
    color: var(--danger-color);
    background-color: var(--danger-background);
    border-color: var(--danger-border);
}


.alert-danger .alert-link {
    color: #491217;
}

/* warning */
.alert-warning {
    color: var(--warning-color);
    background-color: var(--warning-background);
    border-color: var(--warning-border);
}

.alert-warning .alert-link {
    color: #533f03;
}

.alert-info {
    color: var(--info-color);
    background-color: var(--info-background);
    border-color: var(--info-background);
    -webkit-filter: drop-shadow(-4px 0px 7px  var(--info-background));
            filter: drop-shadow(-4px 0px 7px  var(--info-background));
}
.--lunches-details .links a, 
.--lunches-details .links > span{
    -webkit-margin-end: 1.5rem;
            margin-inline-end: 1.5rem;
}
.--lunches-details .links a{
    color: var(--dark);
    padding: 0 10px;
}
.users-grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin-top: calc(1rem + 6px);;
}

.users-grid.--header{
    margin-bottom: 1.2rem;
    border-bottom: 1px solid #d2d2d2;
}
